n-top: 20px;\n      transition: 0.25s ease-in-out;\n    }\n    form {\n      min-width: 300px !important;\n      .input {\n        position: relative;\n        margin: 0 0 0.8em 0;\n        .icon {\n          width: 16px;\n          height: 16px;\n          background: url(../../../images/icon.png) no-repeat -3px -4px;\n          position: absolute;\n          left: 6px;\n          top: 13px;\n          opacity: 0.6;\n          cursor: text;\n          z-index: 10;\n          display: block;\n          transition: 0.25s ease-in-out;\n          &.pass {\n            background: url(../../../images/icon.png) no-repeat -3px -83px;\n          }\n        }\n        input {\n          padding: 8px 10px 8px 28px;\n          font-size: 14px;\n          width: 100%;\n          display: inline-block;\n          background: transparent;\n          border: 0;\n          border-bottom: 2px solid #c2cfdc;\n          outline: 0;\n          box-sizing: border-box;\n          -moz-box-sizing: border-box;\n          font-family: inherit;\n          color: #666666;\n          transition: 0.25s ease-in-out;\n          margin-bottom: 10px;\n          &:disabled {\n            opacity: 0.5;\n            &:hover,\n            &:active,\n            &:focus {\n              border-bottom: 2px solid #c2cfdc;\n            }\n          }\n          &:active,\n          &:focus {\n            border-bottom: 2px solid $color-primary;\n            & + span {\n              opacity: 1;\n            }\n          }\n          &::placeholder {\n            /* Chrome, Firefox, Opera, Safari 10.1+ */\n            color: #999999;\n          }\n        }\n      }\n      .button {\n        margin: 1em 0;\n        height: 40px;\n        width: 50%;\n        display: inline-flex;\n        align-items: center;\n        justify-content: center;\n        background-color: $color-primary;\n        border: none;\n        color: #fff;\n        font-size: 16px;\n        border-radius: 30px;\n        box-shadow: none;\n        outline: none;\n        font-weight: 300;\n        transition: 0.25s ease-in-out;\n        position: relative;\n        cursor: pointer;\n        span.loader {\n          position: absolute;\n          right: 5px;\n        }\n        &:hover {\n          background-color: darken($color-primary, 5%);\n        }\n      }\n      .action {\n        padding: 1.2em 0 0;\n        font-size: 0.93em;\n        p {\n          float: right;\n          text-align: left;\n          font-size: 13px;\n          margin-top: 25px;\n          a {\n            color: $color-primary;\n            text-decoration: underline;\n            font-size: 14px;\n            &:hover {\n              color: darken($color-primary, 5%);\n              text-decoration: underline;\n            }\n          }\n        }\n      }\n      .checkbox {\n        margin: 1em 0;\n        padding: 20px 0;\n        visibility: hidden;\n        text-align: left;\n        &:checked + label:after {\n          -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=100)\";\n          filter: alpha(opacity=100);\n          opacity: 1;\n        }\n      }\n      label[for] {\n        position: relative;\n        padding-top: 2px;\n        padding-left: 10px;\n        cursor: pointer;\n        font-size: 13px;\n        border-radius: 2px;\n        &:before {\n          position: absolute;\n          width: 17px;\n          height: 17px;\n          top: 0px;\n          left: -14px;\n          content: \"\";\n          border: 1px solid #5a6374;\n        }\n        &:after {\n          position: absolute;\n          top: 1px;\n          left: -10px;\n          width: 15px;\n          height: 8px;\n          -ms-filter: \"progid:DXImageTransform.Microsoft.Alpha(Opacity=0)\";\n          filter: alpha(opacity=0);\n          opacity: 0;\n          content: \"\";\n          background-color: transparent;\n          border: solid $color-primary;\n          border-width: 0 0 2px 2px;\n          -webkit-tran