 format(\"truetype\");\n    font-style: normal;\n    font-weight: normal;\n    font-display: swap;\n  }\n  \n  @font-face {\n    font-family: \"Nobel\";\n    src: url(\"/asset/webs/lexus/fonts/nobel/nobel-regular.eot\");\n    src:\n            url(\"/assets/web/lexus/fonts/nobel/nobel-regular.eot?\") format(\"embedded-opentype\"),\n            url(\"/assets/web/lexus/fonts/nobel/nobel-regular.woff\") format(\"woff\"),\n            url(\"/assets/web/lexus/fonts/nobel/nobel-regular.ttf\") format(\"truetype\");\n    font-style: normal;\n    font-weight: normal;\n    font-display: swap;\n  }\n  \n  @font-face {\n    font-family: \"Nobel Book\";\n    src: url(\"/assets/web/lexus/fonts/nobel/nobel-book.eot\");\n    src:\n            url(\"/assets/web/lexus/fonts/nobel/nobel-book.eot?\") format(\"embedded-opentype\"),\n            url(\"/assets/web/lexus/fonts/nobel/nobel-book.woff\") format(\"woff\"),\n            url(\"/assets/web/lexus/fonts/nobel/nobel-book.ttf\") format(\"truetype\");\n    font-style: normal;\n    font-weight: normal;\n    font-display: swap;\n  }\n\n* {\n    margin: 0;\n    padding: 0;\n}\n\nhtml {\n    box-sizing: border-box;\n}\n\n*,\n*:before,\n*:after {\n    margin: 0;\n    padding: 0;\n    box-sizing: inherit;\n}\n/* BODY */\n\nbody {\n    position: relative;\n    color: $color-text;\n    text-align: center;\n    height: 100%;\n    overflow: hidden;\n    // background-image: url('../../../images/login-bg.jpg');\n    background-repeat: no-repeat;\n    background-size: cover;\n    background-position: center top;\n}\n\n.fill {\n    position: absolute;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n}\n\n.login {\n    margin: 0;\n    width: 100%;\n    height: 100%;\n    min-height: 100vh;\n    display: flex;\n    .content {\n        display: flex;\n        position: relative;\n        width: calc(100% - 400px);\n        background-size: cover;\n        background-position: center top;\n        height: 100vh;\n        &:before {\n        